<template>
  <div>
    <fTitle>
      <template #title>Form 表单</template>
      <template #introduce>由输入框、选择器、单选框、多选框等控件组成，用以收集、校验、提交数据</template>
    </fTitle>
    <sTitle>
      <template #title>综合demo</template>
      <template #introduce>包括各种表单项，比如输入框、选择器、开关、单选框等。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row>
 <el-radio-group v-model="labelPosition" size="small">
  <el-radio-button label="left">左对齐</el-radio-button>
  <el-radio-button label="right">右对齐</el-radio-button>
  <el-radio-button label="top">顶部对齐</el-radio-button>
</el-radio-group>
<div style="margin-top: 20px;"></div>
<el-form
:label-position="labelPosition"
:rules="rules"
ref="ruleForm"
label-width="100px"
:model="takeawayOrders">
<el-form-item label="称呼" prop="name">
  <el-input placeholder="请输入您的称呼" v-model="takeawayOrders.name">
  <el-select v-model="takeawayOrders.sex" slot="append" placeholder="请选择">
      <el-option label="先生" value="1"></el-option>
      <el-option label="女士" value="2"></el-option>
    </el-select>
  </el-input>
</el-form-item>
<el-form-item label="手机" prop="phone">
<el-input placeholder="请输入手机号" v-model="takeawayOrders.phone">
</el-input>
</el-form-item>
<el-form-item label="地址" prop="address">
  <el-autocomplete
      v-model="takeawayOrders.address"
      :fetch-suggestions="querySearch"
      placeholder="请输入或选择地址"
    ></el-autocomplete>
</el-form-item>
<el-form-item label="是否需要餐具" prop="tableware">
  <el-tooltip :content="takeawayOrders.tableware + '餐具'" placement="top">
  <el-switch
    v-model="takeawayOrders.tableware"
    active-value="需要"
    inactive-value="不需要">
  </el-switch>
</el-tooltip>
  </el-form-item>
  <el-form-item v-if="takeawayOrders.tableware==='需要'" label="选择餐具数量" prop="nTableware">
    <el-radio-group v-model="takeawayOrders.nTableware">
      <el-radio label='1'></el-radio>
      <el-radio label='2'></el-radio>
      <el-radio label='3'></el-radio>
      <el-radio label='4'></el-radio>
      <el-radio label='更多请备注'></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="订单备注" prop="remark">
    <el-input type="textarea"
    v-model="takeawayOrders.remark"
    placeholder="备注内容"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="success" @click="submitForm('ruleForm')">提交订单</el-button>
    <el-button @click="resetForm('ruleForm')">重置内容</el-button>
  </el-form-item>
</el-form>
</el-row>
    </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    const validateName = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('称呼不能为空'))
      } else {
        if (!/^[\u4E00-\u9FA5]{2,10}(·[\u4E00-\u9FA5]{2,10}){0,2}$/.test(this.takeawayOrders.name)) {
          return callback(new Error('输入正确的姓名'))
        } else { callback() }
      }
    }
    const validatePhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      } else {
        if (!/^1[3~9]\d{9}$/.test(this.takeawayOrders.phone)) {
          return callback(new Error('输入正确的手机号'))
        } else { callback() }
      }
    }
    const checkAddress = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('地址不能为空'))
      } else {
        callback()
      }
    }
    return {
      labelPosition: 'left',
      takeawayOrders: {
        name: '',
        sex: '1',
        phone: '',
        address: '',
        tableware: '不需要',
        nTableware: '1',
        remark: ''
      },
      rules: {
        name: [
          { validator: validateName, trigger: 'blur' }
        ],
        phone: [
          { validator: validatePhone, trigger: 'blur' }
        ],
        address: [
          { validator: checkAddress, trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    querySearch (queryString, cb) {
      const addressAll = this.addressAll
      const results = queryString ? addressAll.filter(this.createFilter(queryString)) : addressAll
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter (queryString) {
      return (address) => {
        return (address.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    loadAll () {
      return [
        { value: '三全鲜食（北新泾店）', address: '长宁区新渔路144号' },
        { value: 'Hot honey 首尔炸鸡（仙霞路）', address: '上海市长宁区淞虹路661号' },
        { value: '新旺角茶餐厅', address: '上海市普陀区真北路988号创邑金沙谷6号楼113' },
        { value: '泷千家(天山西路店)', address: '天山西路438号' },
        { value: '胖仙女纸杯蛋糕（上海凌空店）', address: '上海市长宁区金钟路968号1幢18号楼一层商铺18-101' },
        { value: '贡茶', address: '上海市长宁区金钟路633号' }]
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('成功提交订单')
          console.log(this.takeawayOrders)
        } else {
          alert('填写正确信息')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  },
  mounted () {
    this.addressAll = this.loadAll()
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    text-align: start;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
  .el-row{
    width: 80%;
    left: 10%;
    .el-select{
    width: 80px;
    }
    .el-autocomplete{
      width: 100%;
    }
  }
    }}
</style>
